export default {
  name: "TheLogin",
  data: function data() {
    var _this = this;

    var checkAge = function checkAge(rule, value, callback) {
      console.info(rule);

      if (!value) {
        return callback(new Error("Âπ¥ÈæÑ‰∏çËÉΩ‰∏∫Á©∫"));
      } // setTimeout(()=>{},1000); Â±ïÁ§∫È™åËØÅËøáÁ®ãÔºåËßÜËßâÊïàÊûúÂ•Ω


