        // Âè¶‰∏Ä‰∏™Â∑≤ËæìÂÖ•Ôºå‰øÆÊîπÊú¨Ë∫´ÔºåÂú®È™åËØÅ
        if (_this.myData.checkPass !== "") {
          _this.$refs.myForm.validateField("checkPass");
        }

